import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { EmployeesService } from '../../services/employees.service';

declare var $ : any;

@Component({
  selector: 'app-body',
  templateUrl: './body.component.html',
})
export class BodyComponent implements OnInit {
  
  file: any;

  valueOfSearch: string = '';
  employee: any = {};

  constructor(
    private _employeesService: EmployeesService,
    private router: Router
  ) {}

  ngOnInit(): void {}

  createNewEmployee(
    id: HTMLInputElement,
    name: HTMLInputElement,
    phoneNumber: HTMLInputElement,
    email: HTMLInputElement,
    hireDate: HTMLInputElement,
    managerId: HTMLInputElement
  ): boolean {

    
    //Close modal
    $('#ModalCreate').modal('hide');

    this.employee = {
      id: id.value,
      name: name.value,
      phoneNumber: phoneNumber.value,
      email: email.value,
      hireDate: hireDate.value,
      managerId: managerId.value,
    };

    this._employeesService
      .createNewEmployee(this.employee, this.file)
      .subscribe((res: any) => {
        console.log(res, 'Created');
        
        //Show new employee
        this.router.navigate(['/employee', res.employee._id]);
      });

    return false;
  }

  searchEmployee(data: string) {
    this.valueOfSearch = data;
  }

  onPhotoSelected(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.file = event.target.files[0];
    }
  }
}
