import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { EmployeesService } from '../../services/employees.service';

declare var $ : any;

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
})
export class EmployeeComponent implements OnInit {
  employee: any = { picture: 'uploads/not-img.png' };

  constructor(
    private _employeesService: EmployeesService,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this._employeesService.getEmployees('').subscribe((res: any) => {
        //Seach the selected employee from a updated list
        for (let e of res.employees) {
          if (e._id === params.id) {
            this.employee = e;
          }
        }

        //Calculates time worked in year
        this.employee.timeWorked =
          new Date().getFullYear() -
          new Date(this.employee.hireDate).getFullYear();
      });
    });
  }

  updateEmployee(
    _id: string,
    id: HTMLInputElement,
    name: HTMLInputElement,
    phoneNumber: HTMLInputElement,
    email: HTMLInputElement,
    hireDate: HTMLInputElement,
    managerId: HTMLInputElement
  ) {
    //Close modal
    $('#ModalUpdate').modal('hide');

    this.employee = {
      id: id.value,
      name: name.value,
      phoneNumber: phoneNumber.value,
      email: email.value,
      hireDate: new Date(hireDate.value),
      managerId: managerId.value,
    };

    this._employeesService
      .updateEmployee(_id, this.employee)
      .subscribe((res: any) => {
        this.employee = res.employee;
        this.ngOnInit();
      });
  }

  deleteEmployee(id: string) {
    this._employeesService.deleteEmployee(id);
  }
}
