import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class EmployeesService {
  
  private employees: Employee[] = [];

  constructor(private http: HttpClient) {
    this.getEmployees('').subscribe((res: any) => {
      this.employees = res.employees;
    });
  }

  getEmployees(value: string) {
    return this.http.get<Employee[]>(
      `http://localhost:3000/employee?value=${value}`
    );
  }

  createNewEmployee(employee: Employee, photo: File) {
    console.log('Creating');

    const fd = new FormData();

    fd.append('id', employee.id);
    fd.append('name', employee.name);
    fd.append('phoneNumber', employee.phoneNumber.toString());
    fd.append('email', employee.email);
    fd.append('hireDate', employee.hireDate.toString());
    fd.append('managerId', employee.managerId);
    fd.append('image', photo);

    return this.http.post('http://localhost:3000/employee', fd);
  }

  updateEmployee(id: string, employee: Employee) {
    console.log('Updating');
    let data = encodeURIComponent(id);
    return this.http.put(`http://localhost:3000/employee/${data}`, employee);
  }

  deleteEmployee(id: string) {
    console.log('Deleting');

    let data = encodeURIComponent(id);
    this.http
      .delete(`http://localhost:3000/employee/${data}`)
      .subscribe((res: any) => {
        console.log(res, 'Deleted');
      });
  }
}

export interface Employee {
  _id?: string;
  id: string;
  name: string;
  picture: string;
  phoneNumber: Number;
  email: string;
  hireDate: Date;
  managerId: string;
}
